import React from 'react';
import logo from './logo.svg';
import './App.css';

import { Button } from "@blueprintjs/core";
import { Column, Cell, Table } from "@blueprintjs/table";

import "@blueprintjs/core/lib/css/blueprint.css";
import "@blueprintjs/icons/lib/css/blueprint-icons.css";
import "@blueprintjs/table/lib/css/table.css";

let webappos = window.webappos;

class MountTable extends React.Component {

  collectMountPointsRecursively(json, arr, path) { // path is with the trailing "/"
    for (let key in json) {
      if (typeof json[key] === "string") {
        arr.push({
          mountPoint: path + key,
          location: json[key]
        });
      }
      else {
        this.collectMountPointsRecursively(json[key], arr, path + key + "/");
      }
    }
    return arr;
  }

  readRegistry() {
    let myThis = this;
    webappos.webcall("webappos.getUserRegistryValue", "fs_mount_points").then((resp) => {
      if (resp.result) {
        console.log("registry data",resp.result);
        myThis.setState({
          mount_points: myThis.collectMountPointsRecursively(resp.result, [], "")
        });
      }
    });
  }

  constructor(props) {
    super(props);

    this.state = {
      mount_points: [],
      supported_fs: []
    };
    let myThis = this;
    webappos.request_scopes("webappos_scopes", "login").then(() => {

      webappos.webcall("webappos.getSupportedFileSystems").then((resp) => {
        if (resp.result) {
          console.log("supported_fs",resp.result);
          myThis.setState({
            supported_fs: resp.result
          }, () => {
            myThis.readRegistry();
          });
        }
      });
    });

  }

  componentDidMount() {
    console.log("state1", this.state);
    /*  let myThis = this;
      webappos.request_scopes("webappos_scopes", "login").then(()=>{
  
        webappos.webcall_and_wait("webappos.getSupportedFileSystems").then((resp)=>{
          if (resp.result) {
            myThis.setState(()=>({
              supported_fs: resp.result
            }), ()=>{
              myThis.updateState();
            });
          }
        });
      });*/
  }

  componentDidUpdate() {
    console.log("state2", this.state);
  }

  render() {
    let myThis = this;
    const mountPointRenderer = (rowIndex) => {
      return <Cell>{this.state.mount_points[rowIndex].mountPoint}</Cell>
    // <select>{this.state.supported_fs.map(item=>(<option value={item}>{item}</option>))}</select>
    };
    const locationRenderer = (rowIndex) => {
      return <Cell>{this.state.mount_points[rowIndex].location}</Cell>
    };
    const xRenderer = (rowIndex) => {
      return <Cell><Button icon="delete"></Button></Cell>
    };
    let rowHeight = 31;
    return <Table numRows={this.state.mount_points.length} enableRowResizing={false}
      maxRowHeight={rowHeight} minRowHeight={rowHeight} defaultRowHeight={rowHeight}
      style={{width:"100%"}} columnWidths={[null,null,40]}
    >
      <Column name="Mount Point" cellRenderer={mountPointRenderer}/>
      <Column name="Location String" cellRenderer={locationRenderer}/>
      <Column name="x" cellRenderer={xRenderer}/>
    </Table>;
  }

}

// TODO: table, allow to change, when ok/apply -> store in registry fs_mount_point each mount point, then re-read the registry and re-collectMountPointsRecursively and setState
// button + adds a new line
// button - for each line

function App() {


  return (
    <div className="App">
        <MountTable />        
    </div>
  );
}



export default App;
